.main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 10px auto;
    padding: 20px 0;
}

.main__button {
    display: block;
    font-size: 20px;
    width: 100px;
    padding: 10px;
    margin: 10px auto;
    background-color: #ccc;
    border: none;
    border-radius: 10px;
    box-shadow: 1px 1px 1px 1px #000;
}

.main__button:hover {
    background-color: #eee;
}

.main__button:active {
    background-color: #333;
    color: #fff;
}